<template>
  <b-container>
    <h4 class="filter-title">{{  title  }}</h4>
    <b-dropdown :id="`dropdown-${title}`" :text="title" class="m-md-2">
        <b-dropdown-item 
            v-for="option in options" :key="option.id"
            :id="option"
            :name="option"
            :value="option"
            v-model="selected"
            @change="updateSelected">
        <label :for="option">{{ option }}</label>
        </b-dropdown-item>
    </b-dropdown>
  </b-container>


</template>

<script>
export default {
    name: "FilterList",
    props: {
        title: {
            type: String,
            required: true
        },
        options: {
            type: Array,
            required: true
        },
    },
    data() {
        return {
            selected: []
        }
    },

    methods: {
        updateSelected: function(){
            this.$emit('changeSelected', this.selected)
        }
    },
}
</script>

<style scoped>
    .filter-title {
        color: #EB455F;
    }
</style>